import React, {Component} from 'react';
import {FullCalendar} from 'primereact/fullcalendar';
import dayGridPlugin from '@fullcalendar/daygrid';
import timeGridPlugin from '@fullcalendar/timegrid';
import interactionPlugin from '@fullcalendar/interaction';
import 'primereact/resources/themes/nova-light/theme.css';
import 'primereact/resources/primereact.min.css';
import 'primeicons/primeicons.css';

import {TabView,TabPanel} from 'primereact/tabview';


export class CalEvents extends Component {

    constructor() {
        super();
        this.state = {
            events: [{ id:'Facility',title:'facility',start:'1-1-2020',end:'1-8-2020'},
            { id:'Raid',title:'Raid',start:'1-5-2020',end:'2-8-2020'},
            { id:'VoidBattle',title:'VoidBattle',start:'1-9-2020',end:'1-15-2020'}
        ],
            options: {
                plugins: [dayGridPlugin, timeGridPlugin, interactionPlugin],
                defaultView: 'dayGridMonth',
                defaultDate: '2020-01-01',
                header: {
                    left: 'prev,next',
                    center: 'title',
                    right: 'dayGridMonth,timeGridWeek,timeGridDay'
                },
                editable: true
            }
        };

    }


	render() {
		return (
            <div>
                <div className="fc">
                    <FullCalendar  events={this.state.events} options={this.state.options} />
                </div>

            </div>
        );
	}
}

export default CalEvents;