import React from 'react';
import { DataTable } from 'primereact/datatable';
import { Row } from 'primereact/row';
import { Column } from 'primereact/column';
import {TabView,TabPanel} from 'primereact/tabview';
import 'primereact/resources/themes/nova-light/theme.css';
import 'primereact/resources/primereact.min.css';
import 'primeicons/primeicons.css';

const events = [
    { EventType:'Facility - Dragonyule',StartDate:'1-1-2020',EndDate:'1-8-2020'},
    { EventType:'Raid - A Clawful Caper',StartDate:'1-5-2020',EndDate:'2-8-2020'},
    { EventType:'VoidBattle - Void Agni',StartDate:'1-9-2020',EndDate:'1-15-2020'}
]

//<div className="p-datatable">
function TableEvents(){
    return (
        <div style={{'border-style': 'solid', 'border-width': '4px'}}>
            <DataTable value={events} >
                <Column field="EventType" header='Event Type' sortable={true} style={{textAlign:'right', width:'8em', 'border-style':'solid','border-color':'Black', 'border-width': '4px'}}  />
                <Column field = "StartDate" header = "Start" sortable={true} style={{textAlign:'right', width:'8em','border-style':'solid','border-color':'Black', 'border-width': '4px'}} />
                <Column field = "EndDate" header = "End" sortable ={true}style={{textAlign:'right', width:'8em','border-style':'solid','border-color':'Black', 'border-width': '4px'}} />
            </DataTable>
        </div>
    );
    }
export default TableEvents;

